@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #30383d;
  background-repeat: no-repeat cover;
  background-size: auto;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.container {
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bg-box {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
}

.flexs {
  text-align: center;
}

input {
  color: white;
  outline: none;
}

.bgcol {
  background-color: #30383d;
}

.cssBorder {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: solid white;
  border-width: 1px;
}

.cssSignin {
  border: chartreuse;
  border-width: 2px;
  background: #00bfa5;
}

.center {
  display: flex;
  justify-content: center;
  position: relative;
  width: 500px;
  height: auto;
}

.white {
  text-decoration: none;
  font-family: "Roboto Mono", monospace, sans-serif;
}

#tsparticles {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.mar {
  margin: auto;
  width: 50%;
}

@media only screen and (max-width: 500px) {
  .mar {
    margin: auto;
    width: 90%;
  }
  .center {
    display: flex;
    justify-content: center;
    position: relative;
    width: 360px;
    height: auto;
  }
}

.bgRank {
  width: auto;
  margin: auto;
  position: relative;
  padding: 1em;
  text-align: center;
}

.br-pill {
  height: 50px;
}

.bgText {
  background-color: rgba(255, 255, 255, 0.8);
  z-index: -1;
}
